<template>
  <div class="ReservationList">
    <b-container>
      <b-row>
        <b-col>
          <div class="searchBar">
            <b-input-group size="lg" class="mb-3">
              <b-form-input placeholder="搜尋房源" v-model="searchValue"></b-form-input>
              <b-input-group-append>
                <b-button type="submit" variant="primary">
                  <b-icon-search></b-icon-search>
                </b-button>
              </b-input-group-append>
            </b-input-group>
          </div>
        </b-col>
        <b-col>
          <b-form-group id="input-month" label-for="input-month" class="mb-2">
            <b-form-select
              id="input-month"
              v-model="monthSelected"
              :options="monthOptions"
              class="mb-2 mr-sm-2 mb-sm-0"
              size="lg"
            ></b-form-select>
          </b-form-group>
        </b-col>
      </b-row>
      <b-row>
        <b-table hover :items="data" :fields="column">
          <template v-slot:cell(id)="data">{{ data.index +1 }}</template>
          <template v-slot:cell(date)="data" v-if="reservationStatus">{{ data.item.date }}{{ data.item.hour }}{{ data.item.time }}</template>
          <template v-slot:cell(member)="data" v-if="reservationStatus">{{ data.item.member }}</template>
          <template v-slot:cell(name)="data" v-if="reservationStatus">{{ data.item.name }}</template>
          <template v-slot:cell(place)="data" v-if="reservationStatus">{{ data.item.county }}{{ data.item.district}}</template>
          <template v-slot:cell(room)="data" v-if="reservationStatus">{{ data.item.room }}</template>
           <template v-slot:cell(check)="data">
             <b-btn variant="outline-primary" @click="data">確認</b-btn>
           </template>
        </b-table>
      </b-row>
    </b-container>
  </div>
</template>

<script>
export default {
  name: 'ReservationList',
  data () {
    return {
      searchValue: '',
      monthSelected: 1,
      monthOptions: [
        { text: '1月', value: 1 },
        { text: '2月', value: 2 },
        { text: '3月', value: 3 },
        { text: '4月', value: 4 },
        { text: '5月', value: 5 },
        { text: '6月', value: 6 },
        { text: '7月', value: 7 },
        { text: '8月', value: 8 },
        { text: '9月', value: 9 },
        { text: '10月', value: 10 },
        { text: '11月', value: 11 },
        { text: '12月', value: 12 }
      ],
      column: [
        {
          key: 'id',
          sortable: true,
          label: '編號'
        },
        {
          key: 'date',
          sortable: true,
          label: '預約日期'
        },
        {
          key: 'member',
          label: '預約者'
        },
        {
          key: 'name',
          label: '房源名稱'
        },
        {
          key: 'place',
          label: '房源地址'
        },
        {
          key: 'room',
          label: '房間名稱'
        },
        {
          key: 'check',
          label: '操作'
        }
      ],
      reservation: [],
      housing: [],
      rooms: [],
      register: [],
      reservationStatus: false,
      status: {},
      data: []

    }
  },
  methods: {
    async getData () {
      const api = `${process.env.VUE_APP_APIPATH}/reservation`
      const cacheData = []
      await this.$http.get(api).then(response => {
        this.reservation = response.data

        this.reservation.forEach((item) => {
          if (item.landlordEmail === this.status.account) {
            cacheData.push({
              date: item.date,
              hour: item.hour,
              time: item.time,
              userId: item.userId,
              houseId: item.houseId,
              member: '',
              name: '',
              county: '',
              district: '',
              room: ''
            })
          }
        })
      })
      // console.log(cacheData)
      await this.$http.get(`${process.env.VUE_APP_APIPATH}/register`).then(response => {
        this.register = response.data
        cacheData.forEach(cache => {
          this.register.forEach((item) => {
            if (item.id === Number(cache.userId)) {
              cache.member = item.email
            }
          })
        })
      })
      // console.log(cacheData)
      await this.$http.get(`${process.env.VUE_APP_APIPATH}/house`).then(response => {
        this.housing = response.data

        cacheData.forEach(cache => {
          this.housing.forEach((item) => {
            if (item.id === Number(cache.houseId)) {
              cache.name = item.name
              cache.county = item.county
              cache.district = item.district
            }
          })
        })
      })
      // console.log(cacheData)
      await this.$http.get(`${process.env.VUE_APP_APIPATH}/rooms`).then(response => {
        this.rooms = response.data

        cacheData.forEach(cache => {
          this.rooms.forEach((item) => {
            if (Number(cache.houseId) === item.houseId) {
              cache.room = item.room
            }
          })
        })
      })
      // console.log(cacheData)
      this.data = cacheData
      this.reservationStatus = true
    }
  },
  created () {
    this.getData()
    this.status = JSON.parse(localStorage.getItem('STATUS'))
  }
}
</script>
